<template>
  <div>
    <el-card class="box-card">
      <el-col :span="24">
        <el-row>
          <el-col :span="24">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
              <el-form-item>
                <el-button type="success" @click="addRole" icon="el-icon-circle-plus-outline">添加角色</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="24" style="margin-top:10px;">
            <el-table :data="tableData" border>
              <el-table-column fixed width="200" prop="ID" label="角色ID" align="center"></el-table-column>
              <el-table-column prop="Name" label="角色名称" align="center"></el-table-column>
              <el-table-column width="200" prop="uploadDh" label="用户分配数" align="center"></el-table-column>
              <el-table-column width="250" prop="CreateTime" label="创建时间" align="center"></el-table-column>
              <el-table-column width="200" fixed="right" label="操作" align="center">
                <template slot-scope="scope">
                  <el-tooltip content="" placement="top">
                    <el-button @click="openVisible1(scope.$index, scope.row)" type="" icon="el-icon-s-comment"
                      circle></el-button>
                  </el-tooltip>
                  <el-tooltip content="删除" placement="top">
                    <el-button @click="openVisible(scope.$index, scope.row)" type="danger" icon="el-icon-delete"
                      circle></el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              background
              layout="prev, pager, next ,sizes"
              :total="TotalCount"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              style="margin-top:10px;"
            ></el-pagination>
          </el-col>
        </el-row>
      </el-col>
    </el-card>
    <!-- 账号管理 -->
    <el-dialog title="添加角色" width="30%" :visible.sync="dialogVisible1">
      <el-form label-width="100px">
        <el-form-item label="选择角色权限">
            <el-select v-model="formInline.value" clearable placeholder="选择角色权限">
                <el-option label="业务办理" value="业务办理"></el-option>
                <el-option label="系统管理" value="系统管理"></el-option>
                <el-option label="安全管理" value="安全管理"></el-option>
                <el-option label="审计管理" value="审计管理"></el-option>
            </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible1 = false">取 消</el-button>
        <el-button type="success" @click="upAjax1">添 加</el-button>
      </div>
    </el-dialog>
    <!-- 设备管理 -->
    <el-dialog title="设备绑定" width="50%" :visible.sync="dialogVisible">
        <el-transfer
            v-model="yesData"
            :props="{key: 'id',label: 'name'}"
            :titles="['未关联', '已关联']"
            @change="handleChange"
            :data="noData"
        ></el-transfer>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="upAjax">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  const axios = require("axios");
  import Qs from 'qs';
  export default {
    data() {
      return{
        formInline1:{
          value1:'',
          value2:'',
          value3:''
        },
        formInline:{
          value1:'',
          value2:''
        },
        tableData:[],
        SeeClickData:'',
        dialogVisible:false,
        dialogVisible1:false,
        noData: [{id:1,name:'测试1'},{id:2,name:'测试2'},{id:3,name:'测试3'}],
        yesData: [],
        Page:1,
        PageSize:10,
        QueryWhere:'',
        UserName:'',
        SessionKey:'',
        TotalCount:''
      }
    },
    methods:{
      onSubmit(){
          console.log('查询')
      },
      handleSizeChange(val) {
        this.PageSize = val
        this.getData()
      },
      handleCurrentChange(val) {
        this.Page = val
        this.getData()
      },
      openVisible(tab, event) {
        this.SeeClickData = event
        this.dialogVisible = true
        console.log(this.SeeClickData)
        // this.$confirm('您将激活用户 gao_970707 并延长有效期, 是否继续?', '提示', {
        //   confirmButtonText: '确定',
        //   cancelButtonText: '取消',
        //   type: 'warning'
        // }).then(() => {
        //   this.$message({
        //     type: 'success',
        //     message: '删除成功!'
        //   });
        // }).catch(() => {
        //   this.$message({
        //     type: 'info',
        //     message: '已取消删除'
        //   });          
        // });
      },
      //添加角色
      addRole(){
          this.dialogVisible1 = true
      },
      upAjax1(){
        console.log('添加')
        // this.dialogVisible1 = false
        this.$message({
            type: 'success',
            message: '角色添加成功'
        }); 
      },
      upAjax(){
        console.log('保存')
        this.dialogVisible = false
      },
      handleChange(value, direction, movedKeys) {
        console.log(value);
      },
      getData(){
          axios({
            method: "post",
            url: http+"api/ExamineWeb/GetRoleList",
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: Qs.stringify({
                Page: this.Page,
                PageSize: this.PageSize,
                QueryWhere:this.QueryWhere,
                UserName:this.UserName,
                SessionKey:this.SessionKey
            })
        })
        .then(res => {
            console.log(res);
            if (res.data.IsSuccess) {
                this.TotalCount = res.data.TotalCount
                this.tableData = res.data.Data
            }
        })
        .catch(error => {
            this.$message({
                showClose: true,
                message: "服务器错误",
                type: "error"
            });
        });
      }
    },
    mounted(){
        this.UserName = sessionStorage.getItem("UserName")
        this.SessionKey = sessionStorage.getItem("SessionKey")
        this.getData()
    }
  }
</script>
<style scoped>
  .box-card{
    padding: 20px;
  }
  .el-icon-success {
    color: yellowgreen;
    font-size: 20px;
  }

  .el-icon-error {
    color: red;
    font-size: 20px;
  }
  .dialog{
    padding: 20px;
  }
</style>